@font-face {
  font-family: "CinzelDecorative";
  src: local(CinzelDecorative), url("/src/fonts/CinzelDecorative.ttf") format("truetype");
}

body {
  margin: 0;
  height: 100vh;
  width: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
  font-family: "MagicBeauty";
}

span,
p {
  font-family: "CinzelDecorative";
  cursor: default;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

button {
  cursor: pointer;

  &:disabled {
    cursor:not-allowed;
  }
}
